import Featured from '@/components/main/Featured.vue';
import Projects from '@/components/main/Projects.vue';
import Resume from '@/components/main/Resume.vue';
import Contact from '@/components/main/Contact.vue';
import About from '@/components/main/About.vue';
import Swatch from '@/style/ts/Swatch';
var Sections = [
    {
        path: "/projects",
        name: "Projects",
        icon: "./assets/projects.min.svg",
        color: Swatch.projects,
        component: Projects
    },
    {
        path: "/featured",
        name: "Featured",
        icon: "./assets/featured.min.svg",
        color: Swatch.feature,
        component: Featured
    },
    {
        path: "/resume",
        name: "Rsum",
        icon: "./assets/resume.min.svg",
        color: Swatch.resume,
        component: Resume
    },
    {
        path: "/contact",
        name: "Contact",
        icon: "./assets/contact.min.svg",
        color: Swatch.contact,
        component: Contact
    },
    {
        path: "/about",
        name: "About",
        icon: "./assets/about.min.svg",
        color: Swatch.about,
        component: About
    },
];
// NOTE: the method signature is true evil, this is better for everyone
var SectionMap = {
    featured: Sections.filter(function (elem) { return elem.component == Featured; })[0],
    projects: Sections.filter(function (elem) { return elem.component == Projects; })[0],
    resume: Sections.filter(function (elem) { return elem.component == Resume; })[0],
    contact: Sections.filter(function (elem) { return elem.component == Contact; })[0],
    about: Sections.filter(function (elem) { return elem.component == About; })[0],
};
export default Sections;
export { Sections, SectionMap };



