import Vue from 'vue';
import NavEventBus from '@/scripts/nav/NavEventBus';
import Events from '@/scripts/nav/Events';
import Breakpoints from '@/style/ts/Breakpoints';
import Swatch from '@/style/ts/Swatch';
export default Vue.extend({
    name: "NavItem",
    data: function () {
        return {
            hover: false
        };
    },
    props: {
        properties: {
            type: Object,
            required: true
        }
    },
    watch: {
        $route: function (to, from) {
            if (this.active) {
                this.emitColorChange();
                //console.log(this.color);
            }
        }
    },
    computed: {
        path: function () { return this.properties.path; },
        name: function () { return this.properties.name; },
        icon: function () { return this.properties.icon; },
        color: function () { return this.properties.color; },
        active: function () {
            var fullPath = this.$route.fullPath;
            var location = fullPath.indexOf(this.path);
            return location != -1;
        },
        palette: function () {
            var fore = Swatch.bright;
            var back = Swatch.medium;
            if (this.active) {
                //fore = this.color; //option 1
                fore = Swatch.dark; // option 2
                back = this.color; // option 2
            }
            if (this.hover) {
                fore = this.color; // option 2
                back = Swatch.dark; // option 1 & 2
            }
            return {
                color: fore,
                background: back
            };
        }
    },
    methods: {
        followLink: function () {
            if (Breakpoints.onTabletOrDown()) {
                this.emitClose();
            }
            this.mouseLeave(); // option 2
        },
        emitClose: function () {
            NavEventBus.$emit(Events.closeNav);
        },
        emitColorChange: function () {
            NavEventBus.$emit(Events.changeColor, this.color);
        },
        mouseEnter: function () { this.hover = true; },
        mouseLeave: function () { this.hover = false; },
    },
    mounted: function () {
        // set the initial colour correctly
        // all NavItems will run this code,
        // but only the current one will change the colour
        if (this.active) {
            this.emitColorChange();
        }
    }
});



